import { useEffect, useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useRouter } from 'next/navigation';

'use client';

interface Dog {
    id: string;
    name: string;
    image: string;
    breed: string;
    price: number;
}

export default function Home() {
    const [dogs, setDogs] = useState<Dog[]>([]);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const router = useRouter();

    useEffect(() => {
        const fetchDogs = async () => {
            try {
                setLoading(true);
                const response = await fetch(`http://localhost:5000/dogs?page=${page}`);
                
                if (!response.ok) {
                    throw new Error('Failed to fetch dogs');
                }
                
                const data = await response.json();
                setDogs(data.dogs);
            } catch (err) {
                setError('Error loading dogs. Please try again.');
                console.error(err);
            } finally {
                setLoading(false);
            }
        };

        fetchDogs();
    }, [page]);

    const handleNextPage = () => {
        setPage(page + 1);
    };

    const handlePrevPage = () => {
        if (page > 1) {
            setPage(page - 1);
        }
    };

    return (
        <main className="min-h-screen p-8">
            <div className="max-w-7xl mx-auto">
                <header className="flex justify-between items-center mb-12">
                    <h1 className="text-3xl font-bold">Dog Adoption Center</h1>
                    <div className="flex gap-4">
                        <Link href="/login" className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition">
                            Login
                        </Link>
                        <Link href="/signup" className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 transition">
                            Sign Up
                        </Link>
                    </div>
                </header>

                {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                        {error}
                    </div>
                )}

                {loading ? (
                    <div className="flex justify-center items-center h-64">
                        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                    </div>
                ) : (
                    <>
                        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
                            {dogs.map((dog) => (
                                <div 
                                    key={dog.id} 
                                    className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition cursor-pointer"
                                    onClick={() => router.push(`/dogs/${dog.id}`)}
                                >
                                    <div className="relative h-56 w-full">
                                        <Image
                                            src={dog.image}
                                            alt={dog.name}
                                            fill
                                            className="object-cover"
                                        />
                                    </div>
                                    <div className="p-4">
                                        <h2 className="text-xl font-bold">{dog.name}</h2>
                                        <p className="text-gray-600">{dog.breed}</p>
                                        <p className="text-green-600 font-semibold mt-2">${dog.price}</p>
                                    </div>
                                </div>
                            ))}
                        </div>

                        <div className="mt-8 flex justify-center gap-4">
                            <button
                                onClick={handlePrevPage}
                                disabled={page <= 1}
                                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300 disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                Previous
                            </button>
                            <span className="px-4 py-2">Page {page}</span>
                            <button
                                onClick={handleNextPage}
                                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300"
                            >
                                Next
                            </button>
                        </div>
                    </>
                )}
            </div>
        </main>
    );
}